.back-box {
    position: relative;
    margin: 30px;
}

.back {
    text-decoration: none;
    font-size: 30px;
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
}

.back:hover {
    color: #d88383;
    text-shadow: grey 2px 2px 2px;
    transition: 0.3s ease-out;
    
}